.btn-hover {
  position: relative;
}
.btn-hover.btn-hover-animate {
  transition: all 0.2s linear 0s;
}
.btn-hover.btn-hover-animate:before {
  content: "\f417";
  font-family: bootstrap-icons !important;
  font-size: 0.8125rem;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  inset-inline-end: 0;
  inset-block-start: 0;
  opacity: 0;
  height: 100%;
  width: 2rem;
  transition: all 0.2s linear 0s;
}
.btn-hover.btn-hover-animate:hover {
  padding-inline-end: 2rem;
}
.btn-hover.btn-hover-animate:hover:before {
  opacity: 1;
  text-indent: 0;
}

.btn-glare {
  border: 1px solid;
  overflow: hidden;
  position: relative;
}
.btn-glare span {
  z-index: 20;
}
.btn-glare:after {
  background: var(--custom-white);
  content: "";
  height: 4.6875rem;
  inset-inline-start: -1.625rem;
  opacity: 0.15;
  position: absolute;
  inset-block-start: -3.125rem;
  transform: rotate(35deg);
  transition: all 600ms cubic-bezier(0.2, 1, 0.25, 1);
  width: 1.875rem;
  z-index: 30;
}

.btn-glare:hover:after {
  inset-inline-start: 100%;
  inset-block-start: 0;
  transition: all 600ms cubic-bezier(0.2, 1, 0.25, 1);
}

.btn-darken-hover {
  position: relative;
}
.btn-darken-hover:hover:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  inset-block-start: 0;
  inset-inline-start: 0;
}

