/* CUSTOM */
/* Start::body */
body {
  font-size: 0.875rem;
  font-family: var(--default-font-family);
  font-weight: var(--default-font-weight);
  background-color: var(--default-body-bg-color);
  color: var(--default-text-color);
  line-height: 1.5;
  text-align: start;
  overflow-x: clip;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow-y: auto;
  padding-left: 0;
}

/* End::body */
.simplebar-track.simplebar-vertical {
  top: 0;
  width: 5px !important;
}

*::-webkit-scrollbar {
  transition: all ease 0.05s;
  background: rgb(var(--light-rgb));
  width: 0;
  height: 3px;
}

*:hover::-webkit-scrollbar-thumb {
  background: rgb(var(--light-rgb));
}

.main-content .container,
.main-content .container-fluid {
  padding-inline-start: 20px;
  padding-inline-end: 20px;
}

a,
button {
  outline: 0 !important;
}

/* Start::basic */
.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

@media (min-width: 992px) {
  .app-content {
    min-height: calc(100vh - 7.5rem);
    margin-block-end: 0;
    margin-block-start: 66px;
    margin-inline-start: 240px;
    transition: all 0.05s ease;
  }
}
/* End::basic */
/* Start::App Content */
.example > .dropdown-menu {
  position: static;
  display: block;
}

/* Start::width */
.min-w-fit-content {
  min-width: fit-content;
}

/* End::width */
/* Start::Scrollspy */
.scrollspy-example {
  height: 12.5rem;
  margin-block-start: 0.5rem;
  overflow: auto;
}

.scrollspy-example-2 {
  height: 21.875;
  overflow: auto;
}

.scrollspy-example-3 {
  height: 13.75rem;
  overflow: auto;
}

.simple-list-example-scrollspy .active {
  background-color: var(--primary-color);
  color: #fff;
}

.scrollspy-example-4 {
  height: 12.5rem;
  margin-block-start: 0.5rem;
  overflow: auto;
}

/* End::Scrollspy */
/* Start::Carousel */
.carousel-inner {
  border-radius: 0.35rem;
}

.carousel-caption {
  color: #fff;
}

/* End::Carousel */
/* Start::navbar */
.fixed-top {
  position: static;
  margin: -1rem -1rem 0rem -1rem;
}

.fixed-bottom {
  position: static;
  margin: 1rem 0rem -1rem -1rem;
}

.sticky-top {
  position: static;
  margin: -1rem -1rem 0rem -1rem;
}

/*End::navbar*/
/* Start::Helpers */
.bd-example-ratios .ratio {
  display: inline-block;
  width: 10rem;
  color: var(--text-muted);
  background-color: var(--primary01);
  border: var(--default-border);
  border-radius: 3px;
}

@media (min-width: 768px) {
  .bd-example-ratios-breakpoint .ratio-4x3 {
    --bs-aspect-ratio: 50%;
  }
}
.bd-example-ratios-breakpoint .ratio-4x3 {
  width: 16rem;
}

/* End::Helpers */
/* Start::Layouts */
.bd-example-row [class^=col],
.bd-example-cssgrid .grid > * {
  padding-block-start: 0.75rem;
  padding-block-end: 0.75rem;
  background-color: rgba(var(--light-rgb), 0.75);
  border: 1px solid var(--default-border);
}

.bd-example-cssgrid .grid > * {
  border-radius: 0.25rem;
}

.bd-example-row-flex-cols .row {
  min-height: 10rem;
  background-color: rgba(var(--light-rgb), 0.5);
}

.grid {
  display: grid;
  grid-template-rows: repeat(var(--bs-rows, 1), 1fr);
  grid-template-columns: repeat(var(--bs-columns, 12), 1fr);
  gap: var(--bs-gap, 1.5rem);
}
.grid .g-start-2 {
  grid-column-start: 2 !important;
}
.grid .g-col-2 {
  grid-column: auto/span 2;
}
.grid .g-start-3 {
  grid-column-start: 3;
}
.grid .g-col-3 {
  grid-column: auto/span 3;
}
.grid .g-col-4,
.grid .g-col-md-4 {
  grid-column: auto/span 4;
}
.grid .g-col-6 {
  grid-column: auto/span 6;
}
.grid .g-start-6 {
  grid-column-start: 6;
}

/* End::Layouts */
.callout {
  padding: 1.25rem;
  margin-block-start: 1.25rem;
  margin-block-end: 1.25rem;
  background-color: rgb(var(--light-rgb));
  border-inline-start: 0.25rem solid var(--default-border);
}

.callout-info {
  background-color: rgba(var(--info-rgb), 0.075);
  border-color: rgba(var(--info-rgb), 0.5);
}

.callout-warning {
  background-color: rgba(var(--warning-rgb), 0.075);
  border-color: rgba(var(--warning-rgb), 0.5);
}

.callout-danger {
  background-color: rgba(var(--danger-rgb), 0.075);
  border-color: rgba(var(--danger-rgb), 0.5);
}

.flex-container div {
  background-color: transparent;
  border: 0;
}
.flex-container div > div {
  background-color: rgb(var(--light-rgb));
  border: 1px solid var(--custom-white);
}

.bd-example-position-utils {
  position: relative;
  padding: 2rem;
}
.bd-example-position-utils .position-relative {
  height: 12.5rem;
  background-color: var(--default-background);
}
.bd-example-position-utils .position-absolute {
  width: 2rem;
  height: 2rem;
  background-color: var(--primary01);
  border-radius: 0.375rem;
