/* Start::avatar styles */
.avatar {
  position: relative;
  height: 36px;
  width: 36px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  color: #fff;
  font-weight: 500;
}
.avatar a.badge:hover {
  color: #fff;
}
.avatar img {
  width: 100%;
  height: 100%;
  border-radius: 3px;
}
.avatar.avatar-rounded {
  border-radius: 50%;
}
.avatar.avatar-rounded img {
  border-radius: 50%;
}
.avatar.avatar-radius-0 {
  border-radius: 0;
}
.avatar.avatar-radius-0 img {
  border-radius: 0;
}
