/* Start::header notification dropdown */
.header-notification-text {
  max-width: 14.5rem;
}
@media (max-width: 575px){
  .main-header-container .main-header-dropdown {
    width: 100%;
    // inset-inline-start: 29px !important;
    // inset-inline-end: 5px !important;
}
}
/* Start::header notification dropdown */

