@media (min-width: 992px) {
    [data-nav-style=menu-click][data-nav-layout=horizontal] {
        &[data-theme-mode="dark"] {
            .landing-body .app-sidebar .slide.has-sub.open .slide-menu.child1::before {
                border-block-start-color: rgba(0, 0, 0, 0.1);
                border-inline-start-color: rgba(0, 0, 0, 0.1);
            }

            &[dir="rtl"] {
                .slide.has-sub.open .slide-menu.child1 {
                    &::before {
                        border-inline-end-color: var(--border);
                        border-inline-start-color: transparent !important;
                    }
                }
            }
        }


        .landing-body {

            .main-content .container,
            .main-content .container-fluid {
                padding-inline-start: 20px;
                padding-inline-end: 20px;
            }

            // @media (min-width: 992px) {
            //     .container {
            //         max-width: 85%;
            //     }
            // }

            .app-sidebar {
                border-block-end: 1px solid $primary-05;

                .side-menu__item .side-menu__angle {
                    color: #fff;
                }

                .slide.has-sub.open {
                    .slide-menu {

                        &.child1,
                        &.child2,
                        &.child3 {
                            .slide .side-menu__item {
                                .side-menu__angle {
                                    color: $menu-prime-color !important;
                                }

                                &:hover,
                                &.active {
                                    color: $primary;

                                    .side-menu__angle {
                                        color: $primary !important;
                                    }

                                    &:before {
                                        border-color: $primary !important;
                                    }
                                }
                            }
                        }
                    }
                }

                .horizontal-logo {
                    padding: 0.5rem 0;

                    .header-logo img {
                        height: 2.4rem !important;
                        line-height: 2rem;
                    }
                }

                .side-menu__item {
                    border-radius: 0.25rem;
                    padding: 0.3rem;
                }

                .slide-menu.child1 {
                    border-radius: 0.25rem;
                    padding: 0.55rem 0.3rem;
                }

                .slide-menu.child1 .side-menu__item {
                    padding: 0.33rem 1.6rem;
                }

                .slide.has-sub.open .slide-menu.child1 {
                    overflow: visible !important;

                    &::before {
                        content: "";
                        position: inherit !important;
                        inset-block-start: -7px;
                        inset-inline-start: 10%;
                        width: 13px;
                        height: 13px;
                        z-index: 99999;
                        border: 1px solid transparent;
                        border-block-start-color: var(--border);
                        border-inline-start-color: var(--border);
                        transform: rotate(45deg);
                        background-color: $custom-white;
                    }
                }

                .side-menu__item {
                    padding: 18px 16px !important;
                }

                .side-menu__item.active,
                .side-menu__item:hover {
                    background-color: transparent !important;
                }

                .slide-menu.child1 .slide .side-menu__item:before {
                    inset-block-start: 0.838rem;
                    inset-inline-start: 0.65rem;
                }

                .side-menu__item:hover .side-menu__angle {
                    color: $primary !important;
                }
            }

            .app-sidebar {
                .side-menu__label {
                    color: #fff;
                    opacity: 0.8;
                    font-size: 14px !important;
                    font-weight: 400 !important;
                }

                .side-menu__item {

                    &.active,
                    &:hover {
                        background-color: $primary;
                        color: $primary;

                        .side-menu__label,
                        .side-menu__angle {
                            color: #fff !important;
                            opacity: 1 !important;
                        }
                    }
                }
            }
        }
    }

    .flex-none {
        flex: none;
    }
}

.landing-body {
    background-color: $custom-white;

    .container {
        max-width: 1320px;
    }

    .app-sidebar {
        .side-menu__item {
            padding: 0.8rem 1rem;

            &.active,
            &:hover {
                color: $primary !important;
            }
        }
    }

    .ht-250 {
        height: 250px;
    }

    .main-img {
        position: relative;
        bottom: 250px;
    }

    .feature-icon {
        width: 40px;
        height: 40px;
        display: flex;
        position: relative;
        border-radius: 6px;
        justify-content: center;
        align-items: center;
    }

    .fq-body {
        img {
            height: 350px;
            width: 450px;
        }
    }

    .main-img img {
        height: 430px;
        width: 600px;
        background-color: rgba(var(--primary-rgb), 0.2);
        background-size: cover;
        background-position: center;
        padding: 5px;
        border-radius: 10px;
    }

    .btn.btn-w-lg {
        min-width: 110px;
    }

    .btn-w-xl {
        min-width: 150px;
    }

    .switchericon {
        font-size: 1.2rem;
        border-radius: 3px;
        padding: 0 1rem;
        box-shadow: none;
        font-weight: 400;
        color: $white;
        margin-block-start: 0.15rem;
        opacity: 0.8;
    }

    .app-sidebar.sticky.sticky-pin {
        border-block-end: 1px solid $default-border;

        .switchericon {
            color: $default-text-color;
        }
    }

    .app-sidebar.sticky.sticky-pin .side-menu__item.active {
        font-weight: normal;

        .side-menu__label {
            color: $primary !important;
        }
    }

    #highlights .card {
        border-inline-start: 5px solid $primary-06;
        border-radius: 5px;
    }

    .social-list {
        .social {
            background-color: $primary-01;
            color: $primary;
            padding: 0.375rem 0.5rem;
            border-radius: 50px;
        }
    }

    .testimonal-section {
        background-image: url("../images/landing/7.jpg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        inset-block-start: 0;
        inset-inline-start: 0;
        inset-inline-end: 0;
        position: relative;
        border-radius: 0px;

        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            z-index: 0;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: $primary-07;
        }
    }

    .card-aside-column {
        min-width: 5rem;
        width: 30%;
        -ms-flex: 0 0 30%;
        flex: 0 0 30%;
        background: no-repeat center/cover;
        border-radius: 3px;
        height: auto;
    }

    .bg-fixed-white {
        background-color: #fff !important;
        color: #000 !important;
    }

    .services-statistics {
        .counter-icon {
            margin-block-end: 0.2rem;
            // margin-block-start: 14px;
            display: inline-flex;
            width: 4.5rem;
            height: 4.5rem;
            padding: 12px 10px;
            border-radius: 3px;
            text-align: center;
            // border: 1px solid $default-border;
            // border-radius: 50px;
        }
    }

    .card-aside {
        -ms-flex-direction: row;
        flex-direction: row;
    }

    .accordion.accordion-primary .accordion-button.collapsed:after {
        background-color: $primary-01;
        color: $primary;
    }

    .featur-icon {
        width: 75px;
        height: 75px;
        padding: 14px;
        border-radius: 50px;
        background-color: rgba(255, 255, 255, 0.05);
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
        margin: 0 auto;
    }

    .feature-logos {
        display: flex;
        justify-content: center;
    }

    .counter-icon {
        border: none;
        width: 4.5rem;
        height: 4.5rem;
        margin-block-end: 0.5rem;
    }

    .landing-Features {
        position: relative;
        width: 100%;
        height: 100%;
        inset-block-start: 0;
        background-image: url("../images/landing/8.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        z-index: 9;

        &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba(var(--primary-rgb), 0.9);
            top: 0px;
            z-index: -1;
        }
    }

    .app-sidebar .slide-menu.child1 .side-menu__item.active:before {
        width: 0.35rem;
        height: 0.35rem;
        border: 0px;
        border-radius: 1.0625rem;
        background-color: $primary;
        inset-block-start: 12px !important;
    }

    @media (min-width: 992px) {
        .app-sidebar {
            inset-block-start: 0;
            padding: 0.5rem 0;
            height: auto !important;
            background-color: $primary-01;
            box-shadow: none;
            border-inline-end: 0;

            .main-sidebar {
                height: auto !important;
            }

            &.sticky.sticky-pin {
                background-color: $custom-white;
                box-shadow: 0 0.25rem 1rem $black-1;

                .side-menu__item {
                    .side-menu__angle {
                        color: $menu-prime-color;
                    }

                    &:hover {
                        .side-menu__angle {
                            color: $primary !important;
                        }
                    }
                }

                &.app-sidebar .side-menu__label {
                    color: $custom-black;
                }

                .landing-logo-container .horizontal-logo {
                    .desktop-white {
                        display: none;
                    }

                    .desktop-logo {
                        display: block;
                    }
                }

                &.app-sidebar .side-menu__item:hover {
                    .side-menu__label {
                        color: $primary !important;
                    }
                }
            }

            &.app-sidebar .slide.has-sub.open .slide-menu.child1::before {
                border-block-start-color: $default-border !important;
                border-inline-start-color: $default-border !important;
            }
        }

        .app-header {
            display: none;
        }

        .main-sidebar-header {
            display: block !important;
        }

        .main-menu-container {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .slide-left,
            .slide-right {
                display: none;
            }
        }

        .main-content {
            padding: 0;
        }

        .landing-logo-container {
            .horizontal-logo .header-logo {
                .desktop-logo {
                    display: none;
                }

                .desktop-white {
                    display: block;
                }
            }
        }
    }

    @media (max-width: 991.98px) {

        .app-sidebar .side-menu__item.active,
        .app-sidebar .side-menu__item:hover {
            background-color: transparent;
        }

        .main-menu-container .main-menu {
            padding-inline-start: 20px !important;
        }

        .app-sidebar {
            .slide {
                padding: 0;
            }
        }

        .landing-logo-container {
            .horizontal-logo .header-logo {
                .desktop-logo {
                    display: none !important;
                }

                .desktop-white {
                    display: none !important;
                }
            }
        }
    }

    .main-menu-container .main-menu {
        padding-inline-start: 0;
    }

    .app-content {
        margin-block-start: 4rem;
    }

    .section {
        padding: 3.375rem 0;
        background-size: cover;
        position: relative;
    }

    .landing-banner {
        position: relative;
        width: 100%;
        height: 37rem;
        inset-block-start: 0;
        background-image: url("../images/landing/1.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: $primary-08;
        }

        .main-banner-container {
            padding: 4rem 3rem;
        }

        .landing-banner-heading {
            line-height: 1.25;
            text-shadow: 1px 1px $black-1;
            font-size: 3rem;
            font-weight: 600;
            color: $white;
        }
    }

    @media (max-width: 495px) {
        .landing-page .landing-top-header {
            height: 61rem;
        }
    }

    @media (max-width:767.98px) {
        .landing-main-image {
            display: none;
        }

        .landing-banner {
            height: 34rem;

            .main-banner-container {
                padding: 1rem;
            }
        }
    }

    @media (max-width:400px) {
        .landing-banner {
            height: 45rem;
        }
    }

    @media (max-width: 480px) {
        .landing-banner {
            .section {
                padding: 0.375rem 0;
            }
        }
    }

    .landing-main-image {
        position: relative;
        z-index: 10;

        img {
            z-index: 11;
            position: relative;
            width: 27.063rem;
            height: 26.063rem;
            margin-block-start: -10px;
        }

        &:before {
            content: "";
            position: absolute;
            width: 24.5rem;
            height: 24.5rem;
            background-color: $black-1;
            opacity: 0.3;
            inset-inline-start: 5rem;
            inset-block-start: 0rem;
            border-radius: 50%;
            box-shadow: 0 3rem 10rem $white-1 !important;
        }

        &:after {
            content: "";
            position: absolute;
            width: 12.5rem;
            height: 12.5rem;
            background-color: transparent;
            border: 0.125rem dashed $primary-05;
            opacity: 0.3;
            inset-inline-start: 5.1rem;
            inset-block-end: 4.4rem;
        }
    }

    .landing-page-wrapper {
        position: relative;
        min-height: calc(100vh - 3.4rem);
    }

    .main-landing-image {
        width: 37.5rem;
        padding: 0.5rem;
        border-radius: $default-radius;
        background-color: $primary-01;
    }

    .brand-img-white {
        display: block;
    }

    .brand-img-light {
        display: block;
    }

    .app-sidebar .side-menu__label {
        font-family: 600;
    }

    .landing-title {
        background: linear-gradient(to right, transparent, #8760fb, transparent);
        height: 1px !important;
        margin: 0px auto;
        width: 70px !important;
        margin-block-end: 10px;
    }

    // .landing-section-heading {
    //   position: relative;

    //   &:before {
    //     content: "";
    //     position: absolute;
    //     width: 60%;
    //     height: 0.25rem;
    //     background-image: linear-gradient(to right,
    //         $success,
    //         rgba(var(--success-rgb), 0.1));
    //     opacity: 0.5;
    //     inset-block-start: -0.625rem;
    //     border-radius: 3.125rem;
    //     inset-inline-end: 0;
    //   }
    // }

    .landing-missions,
    .landing-missions-dark {
        position: relative;
        overflow: hidden;
        z-index: 1;
        box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.05) !important;

        &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 0.15rem;
            inset-block-start: 0;
            inset-inline-start: 0;
            border-radius: 3.125rem;
        }

        svg {
            width: 1.5rem;
            height: 1.5rem;
        }
    }

    .customize-image img {
        width: 30.5rem;
        height: 20rem;
        margin-block-start: 20px;
        padding: 15px;
        position: relative;
    }

    .landing-footer {
        background-color: rgb(35, 35, 35);
        border-bottom: 1px solid rgba(255, 255, 255, 0.05);

        .landing-footer-list {
            li {
                margin-bottom: 0.5rem;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    .landing-main-footer {
        background-color: rgb(35, 35, 35);
    }

    .landing-testimonials .swiper-pagination-bullet {
        background: $primary !important;

        &:active {
            background: $primary !important;
        }
    }

    .team-card {
        transition: box-shadow 0.2s ease-in;

        &:hover {
            box-shadow: 0 .25rem 1rem 0 rgba(0, 0, 0, 0.05);
            transition: box-shadow 0.2s ease-in;
        }

        .team-avatar {
            box-shadow: 0 0 0 .5rem rgba(0, 0, 0, 0.05);
        }
    }

    .btn-close {
        padding: 1.5rem;
        right: -0.875rem;
    }

    .section-bg {
        background-color: $gray-1;
    }
}

[data-theme-mode="dark"] {
    .landing-body {
        @media (min-width: 992px) {
            .main-menu-container {
                .landing-logo-container {
                    .horizontal-logo .header-logo {
                        .desktop-logo {
                            display: none !important;
                        }
                    }
                }
            }
        }

        @media (max-width: 991.98px) {
            .app-header {
                .main-header-container {
                    .horizontal-logo .header-logo {
                        .toggle-logo {
                            display: none;
                        }

                        .toggle-dark {
                            display: block;
                        }
                    }
                }
            }
        }

        .landing-trusted-companies {
            img {
                filter: imvert(1);
            }
        }

        .team-card {
            &:hover {
                box-shadow: 0 .25rem 1rem 0 rgba(255, 255, 255, 0.05);
            }

            .team-avatar {
                box-shadow: 0 0 0 .5rem rgba(255, 255, 255, 0.05);
            }
        }

        .app-sidebar.sticky.sticky-pin {
            .landing-logo-container .horizontal-logo {
                .desktop-white {
                    display: block;
                }
            }
        }
    }

    .brand-img-light {
        display: block;
    }

    .brand-img-white {
        display: none;
    }

    .section-bg {
        background-color: $black-1;
    }
}

@media (max-width: 420px) {
    .landing-body .landing-banner {
        // height: 37.5rem;

        .main-banner-container {
            padding: 1rem;
        }

        .landing-banner-heading {
            font-size: 2rem;
        }
    }
}

@media (max-width: 992px) {
    .landing-body {
        .app-sidebar .side-menu__item {
            padding: 0.9rem 10rem 0.8rem 1rem;
        }

        .switchericon {
            color: $default-text-color !important;
        }

        .app-sidebar .slide-menu.child1 li,
        .app-sidebar .slide-menu.child2 li,
        .app-sidebar .slide-menu.child3 li {
            padding: 0;
            -webkit-padding-start: 1.5rem;
            padding-inline-start: 0;
            position: relative;
        }
    }

    [dir="rtl"] {
        .landing-body {
            .app-sidebar .side-menu__item {
                padding: 0.8rem 1rem 0.8rem 5rem;
            }
        }
    }
}

[dir="rtl"] {
    .landing-body {
        .testimonial-card {
            direction: rtl;
        }
    }
}

/* Jobs Landing  */
.landing-body.jobs-landing .landing-banner .landing-banner-heading {
    line-height: 1.25;
    font-size: 2.4rem;
    font-weight: 600;
}

.landing-body.jobs-landing .landing-banner {
    height: auto;
}

.custom-form-group {
    position: relative;
    display: flex;
    align-items: center;
}

.custom-form-group .form-control {
    padding-inline-end: 3.75rem;
    padding-inline-start: 1.25rem;
    line-height: 2.25;
}

.custom-form-group .form-control-lg~.custom-form-btn {
    inset-inline-end: 0.7rem;
}

.custom-form-group .custom-form-btn {
    position: absolute;
    inset-inline-end: 0.5rem;
    border-radius: 0.3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $custom-white;
}

.custom-form-group .custom-form-btn .gps-location {
    color: var(--text-muted);
    opacity: 0.6;
    font-size: 1.0625rem;
    line-height: 0;
    margin-inline-end: 10px;
}

.border-end.border-white3 {
    border-color: rgba(255, 255, 255, 0.1) !important;
}

.bg-banner {
    background-color: var(--primary-color);
    color: #fff;
    position: relative;
    overflow: hidden;
    z-index: 1;

    &::before {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        inset-block-start: 0;
        background-image: url(../images/media/backgrounds/7.jpg);
        background-repeat: no-repeat;
        background-position: bottom;
        background-size: cover;
        opacity: 0.1;
        transition: transform ease 0.3s;
        z-index: -1;
    }
}

.bg-banner-1 {
    position: relative;
    inset-block-start: 0;
    height: 100%;
    border-radius: 0 0.3rem 0.3rem 0;
    z-index: 0;
}

.bg-banner-2 {
    position: relative;
    inset-block-start: 0;
    background-image: url(../images/media/backgrounds/4.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0 0.3rem 0.3rem 0;
    z-index: 0;

    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: var(--primary08);
        inset-inline: 0;
        inset-block: 0;
        z-index: -1;
    }
}

.landing-body.jobs-landing {
    .featured-jobs {
        overflow: hidden;
        border: 1px solid $default-border;
    }
}

.featured-card-4 svg {
    width: 1.5rem;
    height: 1.5rem;
}

.feature-style {
    position: relative;
    overflow: hidden;

    &::before {
        content: "";
        position: absolute;
        width: 4rem;
        height: 4rem;
        inset-inline-end: -1.5rem;
        inset-block-end: -1.5rem;
        background-color: $primary-01;
        border-radius: 50%;
    }

    .feature-style-icon {
        position: relative;
        width: 4rem;
        height: 4rem;
        line-height: 4rem;
        font-size: 1.25rem;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        color: $white;
        font-weight: 500;
        margin-block-end: 0.5rem;
    }

    .feature-style-icon img,
    .feature-style-icon svg {
        width: 2.5rem;
        height: 2.5rem;
    }
}

.landing-body.jobs-landing .landing-main-footer .landing-footer-list li {
    display: inline-block;
    padding: 0 12px;
}

.landing-body .landing-main-footer .landing-footer-list li:not(:first-child)::before {
    position: absolute;
    content: "";
    width: 0.3rem;
    height: 0.3rem;
    border: 1px solid var(--white-5);
    border-radius: 50%;
    background-color: transparent;
    inset-inline-start: -0.2rem;
    inset-block-start: 0.5rem;
}

.landing-body .landing-main-footer .landing-footer-list li:not(:first-child) {
    position: relative;
}

.review-quote {
    position: absolute;
    inset-inline-end: 0.8rem;
    inset-block-start: 1rem;
    font-size: 1.875rem;
    line-height: 0;
    color: var(--primary07);
    background-color: transparent;
    padding: .625rem;
}

.blog-badge {
    position: absolute;
    inset-block-start: 1rem;
    inset-inline-end: 1rem;
}

@media (max-width: 767.98px) {
    .browse-jobs-image {
        width: 100%;
        border-radius: 0.375rem 0.375rem 0 0 !important;
